"use client";

import React, { ReactNode, useState, useEffect } from "react";
import { useSession } from "next-auth/react";
import { useRouter } from "next/router";
import ProtectedRoute from "./ProtectedRoute";
import Navbar from "./Navbar";
import Footer from "./Footer";

import AccountSection from "./dashboard/AccountSection";
import PackagesSection from "./dashboard/PackagesSection";
import HistorySection from "./dashboard/HistorySection";
import ReservarPaquete from "./dashboard/ReservarPaquete";

type TabKey = "mi-cuenta" | "mis-paquetes" | "historial" | "reservar";

export default function DashboardLayout({
  children,
}: {
  children?: ReactNode;
}) {
  const { data: session } = useSession();
  const router = useRouter();
  const { tab: queryTab, view, type, sessions, priceId } =
    router.query as Record<string, string>;

  const [tab, setTab] = useState<TabKey>(
    (queryTab as TabKey) || "mis-paquetes"
  );

  useEffect(() => {
    if (typeof queryTab === "string") {
      setTab(queryTab as TabKey);
    }
  }, [queryTab]);

  const tabs: [TabKey, string][] = [
    ["mi-cuenta", "Mi cuenta"],
    ["mis-paquetes", "Mis paquetes"],
    ["historial", "Historial"],
    ["reservar", "Reservar"],
  ];

  function renderContent() {
    // 1) Si el usuario hizo click en "Lo quiero"
    if (
      view === "reservar-paquete" &&
      type &&
      sessions &&
      priceId
    ) {
      const num = parseInt(sessions, 10) || 0;
      return (
        <ReservarPaquete
          type={type}
          sessions={num}
          priceId={priceId}
        />
      );
    }

    // 2) Si no, renderizamos seg√∫n la pesta√±a activa
    switch (tab) {
      case "mi-cuenta":
        return <AccountSection />;
      case "mis-paquetes":
        return <PackagesSection />;
      case "historial":
        return <HistorySection />;
      case "reservar":
        // opcional: si quieres que ‚ÄúReservar‚Äù sin par√°metros muestre algo distinto
        return <PackagesSection />;
      default:
        return <PackagesSection />;
    }
  }

  return (
    <ProtectedRoute>
      <Navbar />

      <div className="dashboard-container py-4">
        <h2 className="text-center mb-4">
          Hola, {session?.user?.name || "Usuario"}
        </h2>

        <ul className="nav nav-tabs justify-content-center mb-4">
          {tabs.map(([key, label]) => (
            <li key={key} className="nav-item">
              <button
                className={`nav-link ${tab === key ? "active" : ""}`}
                onClick={() => {
                  // Al cambiar pesta√±a borramos view=reservar-paquete
                  router.push(
                    {
                      pathname: "/dashboard",
                      query: { tab: key },
                    },
                    undefined,
                    { shallow: true }
                  );
                }}
              >
                {label}
              </button>
            </li>
          ))}
        </ul>

        {renderContent()}
      </div>

      <Footer />
    </ProtectedRoute>
  );
}
